<template>
  <div id="app">
    <div v-if="isMobile">
      <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
    created(){
      if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)){
        this.isMobile = true
        return
      }else {
        // window.location.href = 'http://192.168.1.150:8080/#/'
        window.location.href = 'http://www.wofew.com/'
      }
  },
  data(){
    return {
      isMobile: false      
    }
  }
}
</script>

<style>
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px; */
}
</style>



