<template>
  <div @touchmove="setBreadRed()" class="layout">
    <div class="topFix">
        <span @click="showBreadNav = !showBreadNav" v-show="!showBreadNav" class="iconfont nav icon-mianbaoxiedaohangxiaoxian"></span>
        <span @click="showBreadNav = !showBreadNav" v-show="showBreadNav" class="iconfont nav icon-guanbi"></span>
        <img class="logo" @click="refresh()" src="../assets/wofew top logo@2x.png" alt="沃菲数字营销">
        <transition name="slide-down">
            <div class="breadNav" v-show="showBreadNav">
                <ul>
                    <li @click="scrollTop(index)" class="breadLi" v-for="(item,index) in breadList" :key="index">
                        <a :class="{red:breadRedIndex == index}" class="breadA">{{item.name}}</a>
                    </li>
                </ul>
            </div>
        </transition>
    </div>
    <div class="mainContent">
        <!-- LOGO栏 -->
        <div class="brandIntroduction">
            <img class="homeLogo" src="../assets/home logo@2x.png" alt="沃菲数字营销">
            <p class="logan">——— · 用营销，创造价值 · ———</p>
            <p @click="conectUs()" class="connect">
                联系我们
            </p>
        </div>
        <!-- 品牌介绍 -->
        <div ref="introduce" class="introduce">
            <p class="slogan">用营销，创造价值。</p>
            <p class="name">沃菲数字营销</p>
            <p class="line"></p>
            <p class="detail">深圳市沃菲数字营销有限公司是一家专业的策略型整合营销传播机构。
            <br><br>自 2015 年 4 月成立伊始，沃菲持续地为客户提供打造品牌影响力的「品牌推广」、与营造产品热度的「产品整合营销」传播服务。
            <br><br>并积极拓展优质资源，在外媒传播、音频产品销售等服务上也积累了一定经验。
            <br><br>沃菲秉承「专业、进取」的企业文化，志在成为一家「本分，值得信赖」的数字营销公司。</p>
        </div>
        <!-- 业务领域 -->
        <div ref="businessDomain" class="businessDomain">
            <h3 class="h3">业务领域</h3>
            <ul>
                <li class="businessItem clearfix" v-for="(item,index) in domainList" :key="index">
                    {{item.name}}
                    <span @click="showItem(1,index)" v-show="showIndex!=index" class="iconfont ico icon-jia float_right"></span>
                    <span @click="showItem(0,index)" v-show="showIndex==index" class="iconfont ico icon-jianhao float_right"></span>
                    <transition name="show-up" mode="out-in">
                        <ul v-show="showIndex==index" style="margin-top:5px;">
                            <li class="subItem" v-for="(i,c_index) in item.content" :key="c_index">
                                {{i}}
                            </li>
                        </ul>
                    </transition>
                </li>
            </ul>
        </div> 
        <!-- 合作品牌 -->
        <div ref="cooperativeBrand" class="cooperativeBrand">
            <h3>合作品牌</h3>
            <p class="redLine"></p>
            <ul class="clearfix ul">
                <li class="imgLi float_left" v-for="(item,index) in imgList" :key="index">
                    <a :href="item.url">
                        <img class="brandImg" :src="item.src">
                    </a>
                </li>
            </ul>
        </div>
        <!-- 执行方案 -->
        <div ref="executionPlan" class="executionPlan">
            <h3>执行案例</h3>
            <p class="redLine"></p>
            <ul style="padding-bottom:46px;">
                <li class="outLi" v-for="(item,index) in planList" :key="index">
                    <a class="p_a" :href="item.url">
                        <img class="p_img" :src="item.src" :alt="item.title">
                        <p class="p_title">{{item.title}}</p>
                    </a>
                </li>
            </ul>
        </div>
        <!-- 联系我们 -->
        <div ref="contactUs" class="contactUs" >
            <h3>联系我们</h3>
            <p class="redLine"></p>
            <div class="way">
                <p class="name">电子邮件</p>
                <p class="addresss">BD@wofew.com</p>
                <p class="name">地址</p>
                <p class="addresss">广东省深圳市南山区中山园路<br>
                 TCL国际 E 城 G2栋 5楼沃菲数字营销</p>
            </div>
        </div>
        <!-- 底栏 -->
        <div ref="footer" class="footer">
            <span class="float_left wofei">© 2017 WOFEW<br><a style="color: #9fa0a0;" target="_blank" href="http://www.miitbeian.gov.cn/publish/query/indexFirst.action"><span style="font-size:12px;">粤ICP备16076245号-1</span></a></span>
            <a class="float_right ico iconfont icon-tuite" href="javascript:void(0);"></a>       
            <a class="float_right ico iconfont icon-weixin" href="javascript:void(0);"></a>
            <a class="float_right ico iconfont icon-weibo" href="https://weibo.com/u/6216872457"></a>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 业务领域组
      domainList: [
        {
          name: "品牌传播策略",
          content: ["· 品牌咨询", "· 产品传播", "· 内容营销", "· 舆情监测"]
        },
        {
          name: "音频领域业务",
          content: ["· 音频渠道拓展", "· 音频嘉年华主办"]
        },
        {
          name: "海外传播战略",
          content: ["· 海外媒体传播", "· 海外产品众筹"]
        },
        {
          name: "其他领域",
          content: ["· 广告设计／制作", "· 品牌 TVC 制作", "· 会展会务"]
        }
      ],
      showIndex: 999,
      // 合作品牌图片
      imgList: [
        {
          src: require("../assets/coo_brand/01.png"),
          url: "http://www.oneplus.cn/"
        },
        {
          src: require("../assets/coo_brand/02.png"),
          url: "https://shop.vivo.com.cn/"
        },
        {
          src: require("../assets/coo_brand/03.png"),
          url: "https://www.meizu.com/"
        },
        {
          src: require("../assets/coo_brand/04.png"),
          url: "https://www.opposhop.cn/"
        },
        {
          src: require("../assets/coo_brand/05.png"),
          url: "https://www.gionee.com/"
        },
        {
          src: require("../assets/coo_brand/06.png"),
          url: "http://www.nubia.com/index.php"
        },
        {
          src: require("../assets/coo_brand/07.png"),
          url: "https://www.lenovo.com.cn/"
        },
        {
          src: require("../assets/coo_brand/08.png"),
          url: "http://www.motorola.com.cn/"
        },
        {
          src: require("../assets/coo_brand/09.png"),
          url: "http://www.huawei.com/cn/"
        },
        {
          src: require("../assets/coo_brand/10.png"),
          url: "http://www.z0m0.com/"
        },
        {
          src: require("../assets/coo_brand/11.png"),
          url: "http://www.honor.cn/"
        },
        {
          src: require("../assets/coo_brand/12.png"),
          url: "https://www.intel.cn/content/www/cn/zh/homepage.html"
        },
        {
          src: require("../assets/coo_brand/13.png"),
          url: "http://www.ogilvy.com.cn/"
        },
        {
          src: require("../assets/coo_brand/14.png"),
          url: "http://www.benq.com.cn/"
        },
        {
          src: require("../assets/coo_brand/15.png"),
          url: "https://www.baidu.com/"
        },
        {
          src: require("../assets/coo_brand/16.png"),
          url: "http://www.bluefocusgroup.com/"
        },
        {
          src: require("../assets/coo_brand/17.png"),
          url: "http://www.dsconsulting.com.cn/"
        },
        {
          src: require("../assets/coo_brand/18.png"),
          url: "http://www.genedigi.com/"
        },
        {
          src: require("../assets/coo_brand/19.png"),
          url: "https://www.hiby.com/"
        },
        {
          src: require("../assets/coo_brand/20.png"),
          url: "http://www.whizzer-tec.com/"
        },
        {
          src: require("../assets/coo_brand/21.png"),
          url: "http://www.simgot.com/cn/"
        },
        {
          src: require("../assets/coo_brand/22.png"),
          url: "http://cn.cayin.cn/"
        },
        {
          src: require("../assets/coo_brand/23.png"),
          url: "http://www.auglamour.com/"
        },
        {
          src: require("../assets/coo_brand/24.png"),
          url: "http://www.hidizs.com.cn/"
        }
      ],
      // 执行方案
      planList: [
        {
          url: "",
          src: require("../assets/ecu_case/jianianhua@2x.png"),
          title: "2017 深圳第一届音频嘉年华"
        },
        {
          url: "",
          src: require("../../src/assets/ecu_case/meizu@2x.png"),
          title: "魅族配件全案推广"
        },
        {
          url: "",
          src: require("../../src/assets/ecu_case/HelloEar Arc.png"),
          title: "HelloEar Arc 量耳定制耳机米家众筹"
        },
        {
          url: "",
          src: require("../../src/assets/ecu_case/kuleng.png"),
          title: "酷冷至尊官方微博运营"
        },
        {
          url: "",
          src: require("../../src/assets/ecu_case/laisheng.png"),
          title: "徕声 GR-1 耳放淘宝众筹"
        },
        {
          url: "",
          src: require("../../src/assets/ecu_case/haibei.png"),
          title: "海贝 R6 无损 Hi-Fi 音乐播放器京东众筹"
        }
      ],
      // 面包屑导航
      showBreadNav: false,
      breadList: [
        {
          name: "品牌介绍",
          url: "#introduce"
        },
        {
          name: "业务领域",
          url: "#businessDomain"
        },
        {
          name: "合作品牌",
          url: "#cooperativeBrand"
        },
        {
          name: "执行案例",
          url: "#executionPlan"
        },
        {
          name: "联系我们",
          url: "#contactUs"
        }
      ],
      breadRedIndex: 999
    };
  },
  methods: {
    showItem(i, index) {
      if (i) {
        this.showIndex = index;
      } else {
        this.showIndex = 999;
      }
    },
    setBreadRed() {
      /*chrome*/
      let d = document.body.scrollTop;
      /*firefox*/
      let f = document.documentElement.scrollTop;
      let height = d || f;
      let pp = this.$refs.introduce.offsetTop - 100;
      let yw = this.$refs.businessDomain.offsetTop - 50;
      let hz = this.$refs.cooperativeBrand.offsetTop - 80;
      let zx = this.$refs.executionPlan.offsetTop - 70;
      let lx = this.$refs.contactUs.offsetTop - 50;
      let dl = this.$refs.footer.offsetTop - 50;

      if (height >= pp && height < yw) {
        this.breadRedIndex = 0;
      } else if (height >= yw && height < hz) {
        this.breadRedIndex = 1;
      } else if (height >= hz && height < zx) {
        this.breadRedIndex = 2;
      } else if (height >= zx && height < lx) {
        this.breadRedIndex = 3;
      } else if (height > dl) {
        this.breadRedIndex = 4;
      }
    },
    scrollTop(i) {
      this.showIndex = 999;
      this.showBreadNav = !this.showBreadNav;
      let scrollHeight;

      let pp = this.$refs.introduce.offsetTop - 100;
      let yw = this.$refs.businessDomain.offsetTop - 50;
      let hz = this.$refs.cooperativeBrand.offsetTop - 80;
      let zx = this.$refs.executionPlan.offsetTop - 70;
      let lx = this.$refs.contactUs.offsetTop - 50;
      let dl = this.$refs.footer.offsetTop - 50;

      if (i == 0) {
        scrollHeight = pp;
        this.breadRedIndex = 0;
      } else if (i == 1) {
        scrollHeight = yw;
        this.breadRedIndex = 1;
      } else if (i == 2) {
        scrollHeight = hz;
        this.breadRedIndex = 2;
      } else if (i == 3) {
        scrollHeight = zx;
        this.breadRedIndex = 3;
      } else if (i == 4) {
        scrollHeight = lx;
        this.breadRedIndex = 4;
      }
      /*chrome*/
      let d = document.body;
      /*firefox*/
      let f = document.documentElement;
      d.scrollTop = scrollHeight;
      f.scrollTop = scrollHeight;
    },
    conectUs() {
      let lx = this.$refs.contactUs.offsetTop - 50;
      /*chrome*/
      let d = document.body;
      /*firefox*/
      let f = document.documentElement;
      d.scrollTop = lx;
      f.scrollTop = lx;
      this.breadRedIndex = 4;
    },
    refresh() {
      window.location.reload();
      /*chrome*/
      let d = document.body;
      /*firefox*/
      let f = document.documentElement;
      d.scrollTop = 0;
      f.scrollTop = 0;
    },
  },
  computed: {}
};
</script>


<style scoped>
/* 面包屑导航红色 */
.red {
  color: #cf111b !important;
}
/* 业务领域过渡效果 */
.show-up-enter-active {
  transition: all 0.6s ease;
}
.show-up-leave-active {
  /* transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0); */
}
.show-up-enter,
.show-up-leave-to {
  transform: translateY(15px);
  opacity: 0;
}
/* 面包导航过渡效果 */
.slide-down-enter-active {
  transition: all 0.5s ease;
}
.slide-down-leave-active {
  transition: all 0.5s ease;
}
.slide-down-enter,
.slide-down-leave-to {
  transform: translateY(-10px);
  opacity: 0;
}
/* 全部标题 */
h3 {
  font-family: PingFangSC-Medium;
  font-size: 30px;
  color: #2c2728;
  letter-spacing: 0;
  text-align: center;
}
/* 红线 */
.redLine {
  width: 30px;
  height: 1px;
  margin: 0 auto;
  background-color: #cf111b;
  margin-top: 10px;
  margin-bottom: 30px;
}
/* 顶部固定 */
.topFix {
  padding-top: 5px;
  width: 100%;
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
  /* position: relative; */
  background: #ffffff;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2);
}
.nav {
  /* padding: 17px 20px; */
  position: absolute;
  width: 56px;
  text-align: center;
  height: 50px;
  line-height: 50px;
  top: 0;
  left: 0;
}
.logo {
  width: 100px;
  height: 40px;
  display: block;
  margin: 0 auto;
}
/* 面包导航展开 */
.topFix .breadNav {
  width: 100%;
  height: calc(100vh - 50px);
  position: absolute;
  top: 50px;
  left: 0px;
  background-color: #fff;
  z-index: 99;
  border-top: 2px solid #e5e5e5;
}
.topFix .breadNav .breadLi {
  padding: 0px 37px 0 38px;
}
.topFix .breadNav .breadA {
  display: inline-block;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #e5e5e5;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #9fa0a0;
  letter-spacing: 0;
}
/* 内容主题部分 */
.mainContent {
}
.mainContent .homeLogo {
  display: block;
  width: 217px;
  margin: 150px auto 0 auto;
}
.mainContent .logan {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #9fa0a0;
  letter-spacing: 0;
  margin: 21px 0 35px 0;
  text-align: center;
}
.mainContent .connect {
  width: 100px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  background: #cf111b;
  border-radius: 4px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #ffffff;
  letter-spacing: 0;
  margin: 0 auto;
  margin-bottom: 120px;
}
/* 公司介绍 */
.introduce {
  width: 330px;
  height: 520px;
  background: #cf111b;
  border-radius: 4px;
  margin: 0 auto;
  padding: 40px 70px 0px 20px;
}
.introduce .slogan {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #ffffff;
  letter-spacing: 0;
}
.introduce .name {
  font-family: PingFangSC-Medium;
  font-size: 40px;
  color: #ffffff;
  letter-spacing: 0;
}
.introduce .detail {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #ffffff;
  letter-spacing: 0;
}
.introduce .line {
  width: 30px;
  height: 1px;
  background: #ffffff;
  margin: 40px 0 18px 0;
}
/* 业务领域 */
.businessDomain {
  padding: 45px 22px 135px 23px;
}
.businessDomain .businessItem {
  padding: 13px 0px 10px 3.5px;
  border-bottom: 1px solid #e5e5e5;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #2c2728;
  letter-spacing: 0;
}
.businessDomain .businessItem:first-child {
  border-top: 1px solid #e5e5e5;
  margin-top: 20px;
}
.businessDomain .businessItem .subItem {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #9fa0a0;
  letter-spacing: 0;
  line-height: 25px;
  margin-left: 16.5px;
}
.businessDomain .ico {
  padding: 0px 8px;
}
/* 合作品牌 */
.cooperativeBrand {
  padding: 0 40px 40px 41px;
}
.cooperativeBrand ul {
  margin: 0 auto;
}
.cooperativeBrand .imgLi {
  background: #ffffff;
  border-radius: 4px;
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
}
.cooperativeBrand .imgLi:nth-child(3n + 2) {
  margin-left: calc((100vw - 321px) / 2);
  margin-right: calc((100vw - 321px) / 2)
}
.cooperativeBrand .imgLi a {
  width: 100%;
  display: inline-block;
}
.cooperativeBrand .imgLi .brandImg {
  width: 100%;
  display: block;
}
/* 执行方案 */
.executionPlan {
}
.executionPlan .outLi {
  width: 300px;
  height: 225px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 35px;
}
.executionPlan .outLi .p_a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.executionPlan .outLi .p_a .p_img {
  width: 100%;
  height: 169px;
}
.executionPlan .outLi .p_a .p_title {
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #9fa0a0;
  letter-spacing: 0;
  line-height: 56px;
  text-align: center;
}
/* 联系我们 */
.contactUs {
  padding-bottom: 130px;
}
.contactUs .way {
  width: 300px;
  height: 200px;
  padding: 30px 0 0 20px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.01);
}
.contactUs .way .name {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #2c2728;
  letter-spacing: 0;
}
.contactUs .way .addresss {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #9fa0a0;
  letter-spacing: 0;
  margin: 5px 0 30px 0px;
}
/* 底栏 */
.footer {
  height: 80px;
  padding: 0px 37.5px;
  border-top: 1px solid #d4d4d4;
}
.footer .wofei {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #9fa0a0;
  /* line-height: 40px; */
  margin-top: 26px;
}
.footer .ico {
  text-align: center;
  width: 40px;
  line-height: 80px;  
  color: #000;
  padding: 0 12px;
}
</style>



